@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&family=Vollkorn:ital,wght@0,700;1,700&display=swap');

body {
  margin:0;
  font-family:"Manrope", "Vollkorn", -apple-system, 'Roboto', sans-serif;
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale;
}

code {
  font-family:"Manrope", "Vollkorn", Consolas, 'Courier New', monospace;
}


/* common */
html, body{height:100%;}
body{margin:0; padding:0; font-family:'Manrope', sans-serif; font-size:24px; font-weight:500; line-height:1.2; color:#333;}
a{color:inherit; text-decoration:none;}
hr, caption, legend{display:none;}
colgroup{background:none;}
h1, h2, h3, h4, h5, h6{font-size:100%; font-weight:500; line-height:1.2;}
h1, h2, h3, h4, h5, h6, p, ul, ol, li, dl, dt, dd, form, fieldset, table, caption, tbody, tfoot, thead, tr, th, td, figure, figcaption, img{margin:0; padding:0; border:0;}
ul, ol, li{list-style:none;}
table{width:100%; border-spacing:0; border:0;}
p, div, th, td{font-size:100%;}
img, input, select, textarea{vertical-align:middle;}
input, select, textarea, button{font-family:'Manrope', sans-serif; font-size:100%;}
textarea{resize:none;}
input[type="text"],
input[type="password"],
input[type="date"]{width:100%; height:40px; box-sizing:border-box; padding:0 15px; border:1px solid #ccc; background:#fff;}
input[type="password"]{font-family:'Manrope';}
input[type="text"][readonly]{padding:0; border:0;}
input[type="text"]:focus,
input[type="password"]:focus{border-color:#222; outline:none;}
textarea{width:100%; height:200px; box-sizing:border-box; padding:15px; border:1px solid #e6e6e6; background:#fff; -webkit-appearance:none; appearance:none; box-shadow:none;}
input[type="submit"], button{cursor:pointer;}
button{padding:0; -webkit-appearance:none; appearance:none;}
button::-moz-focus-inner{padding:0; border:0;}
select{height:40px; padding:0 40px 0 20px; color:#333; -webkit-appearance:none; appearance:none;}
select::-ms-expand{display:none;}
input::-ms-clear, input::-ms-reveal{display:none;}
::-webkit-input-placeholder{color:#9d9d9d;}
:-ms-input-placeholder{color:#9d9d9d;}

.blind{position:absolute; top:0; left:-9999px; width:0; height:0; font-size:0; line-height:0;}
.skip-nav a{position:absolute; top:-30px; left:0; background:#000; height:30px; line-height:30px; color:#fff; font-size:12px; padding:0 6px;}
.skip-nav a:focus,
.skip-nav a:active{top:0;}
.inner{position:relative; width:67.7083vw; margin:0 auto;}
.wrap{color:#fff; background:#000;}