body{background:#000;}
header{position:fixed; z-index:2; width:100%; padding:80px 0 0;}
header h1{font-size:40px; font-family:'Vollkorn'; font-weight:700; color:#800000;}

.wrap > article{position:relative;}

.main{width:100%; height:100vh;}
.main .inner{position:fixed; left:50%; height:100vh; transform:translateX(-50%);}
.main p{position:absolute; top:50%; left:0; font-size:88px; line-height:1.5; transform:translateY(-60%);}
.main .scroll-down{position:absolute; bottom:12vh; left:0; width:120px; height:120px; overflow:hidden; text-indent:-9999px; border:6px solid #fff; border-radius:50%;}
.main .scroll-down i{position:absolute; top:50%; left:50%; width:50px; height:60px; transform:translate(-50%, -50%);}
.main .scroll-down i:before{position:absolute; top:0; left:50%; width:6px; height:100%; content:''; background:#fff; transform:translateX(-50%);}
.main .scroll-down i:after{position:absolute; bottom:6px; left:50%; width:40px; height:40px; box-sizing:border-box; content:''; border-right:6px solid #fff; border-bottom:6px solid #fff; transform:rotate(45deg) translateX(-50%); transform-origin:left;}

.work-list{padding:200px 0; background:#000;}
.work-list .item{display:flex; justify-content:space-between;}
/* .work-list .item:after{clear:both; display:block; content:'';} */
.work-list .item + .item{margin:120px 0 0;}
.work-list .item h3{position:sticky; top:180px; left:0; z-index:2; align-self:flex-start; font-size:30px; font-weight:700;}
.work-list .item ul{width:960px;}
.work-list .item li{display:flex;}
.work-list .item li:not(:first-child){margin:80px 0 0;}
.work-list .img{position:relative; width:400px; overflow:hidden;}
.work-list .img:after{position:absolute; top:0; left:0; width:100%; height:100%; content:''; background:transparent; transition:all .8s ease-in-out;}
.work-list .img img{width:100%; transition:all .8s ease-in-out;}
.work-list .txt{display:flex; flex-direction:column; flex:1; margin:0 0 0 80px;}
.work-list .txt .info{flex:1;}
.work-list .txt .info .disc{margin:10px 0 0; font-size:18px; color:#c3c3c3;}
.work-list .btn-area{font-size:16px;}
.work-list .btn-area a{display:inline-block; height:40px; box-sizing:border-box; padding:0 30px; line-height:36px; border:2px solid #fff; border-radius:25px;}
.work-list .btn-area a + a{margin-left:15px;}
.work-list .btn-area .ico-link{position:relative; display:inline-block; width:17px; height:17px; margin:0 0 0 5px;}
.work-list .btn-area .ico-link:before{position:absolute; top:5px; right:0; content:''; border-top:9px solid #fff; border-left:9px solid transparent;}
.work-list .btn-area .ico-link:after{position:absolute; top:4px; left:9px; width:2px; height:15px; content:''; background:#fff; transform:rotate(45deg);}

.contact{height:100vh; box-sizing:Border-box; padding:400px 0 260px; background:#000;}
.contact h2{margin:0 0 180px; font-size:80px; font-weight:800;}
.contact dl{display:flex;}
.contact dl + dl{margin:80px 0 0;}
.contact dt{width:510px; font-weight:700;}
.contact dd{font-weight:400;}

@media all and (max-width:1600px){
    .work-list .item ul{width:760px}
}

@media all and (max-width:1366px){
    .inner{width:85%;}

    header{padding:40px 0 0;}

    .main p{font-size:6.4422vw;}
    .main .scroll-down{bottom:10vh; width:80px; height:80px;}
    .main .scroll-down i{height:40px;}
    .main .scroll-down i:after{bottom:2px; width:25px; height:25px;}

    .work-list figure .img{min-width:300px; width:300px;}
    .work-list .btn-area{left:380px;}

    .contact h2{font-size:5.8565vw;}
    .contact dt{width:350px;}
}

@media all and (max-width:1024px){
    .inner{width:95%;}

    .work-list .item h3{font-size:25px;}
    .work-list .item ul{width:760px;}
    .work-list .img{width:39.0625vw;}
    .work-list .txt{margin:0 0 0 40px;}

